.experiencia-section {
  background: #fff;
  border-radius: 0;
  box-shadow: none;
  border: 1px solid #e0e0e0;
  padding: 2.5em 2em;
  max-width: 900px; /* un poco más ancho para layout con imagen */
  margin: 0 auto;
}

.experiencia-title {
  color: #FF0055;
  font-size: 2.2em;
  margin-bottom: 1.2em;
  text-align: left;
  letter-spacing: 2px;
  font-family: 'Ndot 57 Aligned', Arial, sans-serif;
}

.experiencia-item {
  background: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1.2em 1em;
  width: 100%;
}

.experiencia-item.reveal {
  margin-bottom: 1.5em;
}

.experiencia-row {
  display: grid;
  grid-template-columns: 1fr 240px; /* contenido + logo */
  gap: 1.2em;
  align-items: center;
}

.experiencia-content h3 {
  color: #111;
  font-size: 1.3em;
  margin-bottom: 0.5em;
  font-family: 'Ndot 57 Aligned', Arial, sans-serif;
}

.experiencia-texto {
  color: #333;
  font-size: 1.05em;
  margin: 0;
  line-height: 1.6;
}

.experiencia-logo {
  display: flex;
}

.experiencia-link {
  display: block;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
  position: relative;
  margin-bottom: 2.5em;
  cursor: pointer;
}

.experiencia-link:hover .experiencia-item {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  border-color: #FF0055;
}

.experiencia-link:hover .experiencia-content h3 {
  color: #FF0055;
}

.experiencia-link:after {
  content: '';
  position: absolute;
  bottom: -10px;
  right: 20px;
  width: 0;
  height: 2px;
  background-color: #FF0055;
  transition: width 0.3s ease;
}

.experiencia-link:hover:after {
  width: 50px;
}

.logo-placeholder {
  width: 220px;
  height: 140px;
  border: 1.5px dashed #cfcfcf; /* resalta en secciones claras */
  border-radius: 8px;
  color: #888;
  font-family: 'Ndot 57 Aligned', Arial, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fafafa;
}

.logo-img {
  width: 220px;
  height: 140px;
  object-fit: contain;
  border-radius: 8px;
  background: #fff;
}

@media (max-width: 760px) {
  .experiencia-section {
    max-width: 98vw;
    padding: 1.2em 0.5em;
  }
  .experiencia-row {
    grid-template-columns: 1fr; /* apilar en móvil */
  }
  .logo-placeholder {
    width: 100%;
    height: 120px;
  }
}
