.contacto-footer {
  background: #1a1a1a;
  border-radius: 0;
  box-shadow: none;
  border-top: 1px solid #2a2a2a;
  padding: 3em 2em 2em 2em;
  width: 100%;
  margin: 0;
  color: #fff;
  text-align: left;
  /* Anula estilos globales de tarjeta aplicados en App.css */
  max-width: none;
  border-left: none;
  border-right: none;
  border-bottom: none;
  background-image: none !important; /* quita puntos en el footer */
}

.contacto-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  gap: 2em;
}

.contacto-column {
  flex: 1;
  min-width: 250px;
}

.contacto-title {
  color: #FF0055;
  font-size: 1.8em;
  margin-bottom: 1em;
  letter-spacing: 2px;
  font-family: 'DotGothic16', Arial, sans-serif;
}

.contacto-subtitle {
  color: #FF0055;
  font-size: 1.4em;
  margin-bottom: 1em;
  letter-spacing: 1px;
  font-family: 'DotGothic16', Arial, sans-serif;
}

.contacto-texto {
  color: #ccc;
  font-size: 1.1em;
  margin: 0;
  line-height: 1.6;
}

.contacto-social {
  display: flex;
  flex-direction: column;
  gap: 0.8em;
}

.social-link {
  color: #ccc;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  transition: color 0.3s ease;
}

.social-link i {
  margin-right: 0.5em;
  font-size: 1.2em;
}

.social-link:hover {
  color: #FF0055;
}

.copyright {
  margin-top: 3em;
  padding-top: 1.5em;
  border-top: 1px solid #2a2a2a;
  text-align: center;
  font-size: 0.9em;
  color: #888;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
