.proyectos-section {
  background: #fff;
  border-radius: 0;
  box-shadow: none;
  border: 1px solid #e0e0e0;
  padding: 2.5em 2em;
  max-width: 700px;
  margin: 0 auto;
}

.proyectos-title {
  color: #FF0055;
  font-size: 2.2em;
  margin-bottom: 1.2em;
  text-align: left;
  letter-spacing: 2px;
  font-family: 'DotGothic16', Arial, sans-serif;
}

/* Nuevo layout: grid de tarjetas clicables */
.proyectos-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.2em;
}

.project-card {
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  transition: transform 0.18s ease, box-shadow 0.18s ease, border-color 0.18s ease, outline-color 0.18s ease;
}

.project-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 26px rgba(0,0,0,0.08);
  border-color: #d9d9d9;
}

.project-card:focus-visible {
  outline: 2px solid #FF0055;
  outline-offset: 2px;
  box-shadow: 0 0 0 4px rgba(255,0,85,0.12);
}

.project-media {
  aspect-ratio: 16/9;
  background: transparent; /* sin fondo para que no quede banda */
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0; /* la imagen ocupa todo el espacio */
}

/* sutil patrón de puntos suave como guiño al estilo general */
.project-media::after { content: none; }

.project-media img {
  width: 100%;
  height: 100%;
  max-width: none;
  max-height: none;
  display: block; /* elimina hueco por línea base */
  filter: drop-shadow(0 10px 18px rgba(0,0,0,0.14));
}

.project-media.fit-contain img {
  object-fit: contain;
}

.project-media.fit-cover img {
  object-fit: cover;
}

.project-info {
  padding: 0.9em 1em 1.1em 1em;
}

.project-title {
  margin: 0;
  color: #111;
  font-size: 1.2em;
  font-family: 'DotGothic16', Arial, sans-serif;
}

.project-card:hover .project-title {
  color: #FF0055;
}

.project-desc {
  margin: 0.55em 0 0 0;
  color: #333;
  font-size: 1.05em;
  line-height: 1.6;
}

.project-cta {
  display: inline-block;
  margin-top: 0.85em;
  font-family: 'Ndot 57 Aligned', Arial, sans-serif;
  letter-spacing: 1px;
  color: #FF0055;
  border: 1px solid #FF0055;
  border-radius: 8px;
  padding: 0.3em 0.7em;
  transition: background 0.18s ease, color 0.18s ease, border-color 0.18s ease;
}

.project-card:hover .project-cta {
  background: #FF0055;
  color: #fff;
  border-color: #FF0055;
}

@media (min-width: 720px) {
  .proyectos-section {
    max-width: 900px;
  }
  .proyectos-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
