.header-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    height: auto;
    width: 100%;
    background-color: #000; /* negro */
    /* Puntos blancos sobre negro, fijo */
    background-image:
        radial-gradient(rgba(255,255,255,0.45) 1px, transparent 1px),
        radial-gradient(rgba(255,255,255,0.25) 1px, transparent 1px);
    background-size: 48px 48px, 48px 48px;
    background-position: 0 0, 24px 24px;
    background-attachment: fixed;
    padding: 2vw 0 1vw 0;
    gap: 2vw;
    border-bottom: 1px solid #2a2a2a;
}

.header-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 2vw;
    gap: 0.5em;
    position: relative;
}

/* Títulos del header con fuente base (Montserrat/heredada) */
.header-left h1 {
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0;
    color: #fff; /* blanco para contraste */
}

/* Textos normales del header con NDOT 57 */
.header-container p,
.header-container a,
.header-container span,
.header-container li {
    font-family: 'Ndot 57 Aligned', Arial, sans-serif !important;
    color: #fff; /* blanco para contraste */
}

.header-nav {
    display: flex;
    flex-direction: row;
    gap: 1.5em;
    margin-right: 2vw;
    align-items: center;
}

.header-nav a {
    color: #fff;
    text-decoration: none;
    font-size: 1.1em;
    padding: 0.4em 1em;
    border-radius: 8px;
    border: 1px solid #FF0055;
    background: none;
    transition: background 0.2s, color 0.2s, border-color 0.2s;
}

.header-nav a:hover {
    background: #FF0055;
    color: #fff;
    border-color: #FF0055;
}

.header-profile {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
    transition: transform 0.2s ease, filter 0.2s ease;
    border-radius: 8px;
    padding: 0.5rem;
    margin: -0.5rem; /* compensar padding para no mover layout */
}

.header-profile:hover {
    transform: scale(1.02);
    filter: brightness(1.1);
}

.header-profile:active {
    transform: scale(0.98);
}

/* Nombre: usar NDOT 57 */
.header-identity h1 {
    margin: 0;
    font-family: 'Ndot 57 Aligned', Arial, sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
    line-height: 1.1;
    transition: color 0.2s ease;
}

.header-profile:hover .header-identity h1 {
    color: #FFCDD6; /* ligero tinte al hover */
}

/* Sobrenombre: usar Montserrat (forzado) */
.header-identity p {
    margin: 0.2rem 0 0 0;
    font-family: 'Montserrat', Arial, sans-serif !important;
    color: #fff;
    transition: color 0.2s ease;
}

.header-profile:hover .header-identity p {
    color: #FFCDD6;
}

.avatar-grande {
    width: 22vw;
    height: 22vw;
    max-width: 140px; /* un poco más grande */
    max-height: 140px;
    min-width: 68px;
    min-height: 68px;
    z-index: 2;
    border-radius: 50%;
    border: 2px solid #FF0055;
    background: #000;
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.header-profile:hover .avatar-grande {
    border-color: #FFCDD6;
    box-shadow: 0 0 8px rgba(255, 0, 85, 0.3);
}

.header-left p {
    font-size: 1.2vw;
    color: #cccccc;
    margin: 0.2em 0 0 0;
}

.info-animada, .info-oculta {
    position: absolute;
    left: 0;
    top: calc(100% + 8px); /* despegar del header */
    width: max-content;
    min-width: 220px;
    background: #111; /* caja oscura dentro del header negro */
    color: #fff;
    border-radius: 8px;
    border: 1px solid #2a2a2a;
    box-shadow: none;
    padding: 0.9em 1.2em;
    margin-top: 0.6em;
    text-align: left;
    z-index: 1000; /* por encima del resto del header */
}

.info-animada {
    opacity: 1;
    max-height: 200px;
    transform: translateY(0);
    transition: opacity 0.4s ease, max-height 0.4s ease, transform 0.4s cubic-bezier(0.4,0,0.2,1);
    overflow: hidden;
    pointer-events: auto;
}

.info-oculta {
    opacity: 0;
    max-height: 0;
    transform: translateY(-10px);
    transition: opacity 0.4s ease, max-height 0.4s ease, transform 0.4s cubic-bezier(0.4,0,0.2,1);
    overflow: hidden;
    pointer-events: none;
}

/* Cuando el panel está abierto, evita que solape contenido con borde y sombra más marcados */
.with-contact-panel .info-animada {
    box-shadow: 0 14px 36px rgba(0,0,0,0.45);
    border-color: #3a3a3a;
}

/* Estilos de enlaces dentro de info extra */
.info-links {
    display: flex;
    flex-direction: column;
    gap: 0.4em;
    margin-top: 0.4em;
}

.info-links a {
    font-family: 'Montserrat', Arial, sans-serif;
    color: #FFCDD6; /* blanco con leve tinte */
    text-decoration: none;
    border: 1px solid #FF0055;
    border-radius: 8px;
    padding: 0.3em 0.6em;
    transition: background 0.2s, color 0.2s, border-color 0.2s;
}

.info-links a:hover {
    background: #FF0055;
    color: #fff;
    border-color: #FF0055;
}

.info-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.5rem;
}

.info-close {
    background: none;
    border: 1px solid #FF0055;
    color: #fff;
    border-radius: 6px;
    font-size: 1rem;
    line-height: 1;
    width: 28px;
    height: 28px;
    cursor: pointer;
}

.info-close:hover {
    background: #FF0055;
}

/* Asegurar interacción dentro del panel */
.info-animada, .info-oculta {
    pointer-events: auto;
}

@media (max-width: 600px) {
    .header-container {
        flex-direction: column;
        align-items: center;
        padding: 4vw 0 2vw 0;
        gap: 3vw;
    }
    .header-left {
        align-items: center;
        margin-left: 0;
    }
    .header-nav {
        margin-right: 0;
        margin-top: 1em;
    }
    .avatar-grande {
        width: 28vw;
        height: 28vw;
        max-width: 90px;
        max-height: 90px;
        min-width: 48px;
        min-height: 48px;
    }
    .header-left h1 {
        font-size: 5vw;
    }
    .header-left p {
        font-size: 3vw;
    }
}